      0 78%,
      12% 85%,
      23% 90%,
      32% 91%,
      49% 86%,
      67% 79%,
      83% 77%,
      100% 79%,
      100% 100%,
      0% 100%
    );
            clip-path: polygon(
      0 78%,
      12% 85%,
      23% 90%,
      32% 91%,
      49% 86%,
      67% 79%,
      83% 77%,
      100% 79%,
      100% 100%,
