@keyframes animateSeaLion {
  0% {
    transform: rotate(0deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  60% {
    transform: rotate(0deg);
  }
  90% {
    transform: rotate(5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
@keyframes animateOctopusBody {
  50% {
    transform: translateY(-8%);
  }
}
@keyframes animateOctopusLeftTentacle {
  50% {
    transform: rotate(-20deg);
  }
}
@keyframes animateOctopusRightTentacle {
  50% {
    transform: rotate(20deg);
  }
}
@keyframes animateStarfish {
  0% {
    transform: rotate(0deg);
  }
  20% {
    transform: rotate(10deg);
  }
  40% {
    transform: rotate(0deg);
  }
  70% {
    transform: rotate(-15deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

