    justify-content: center;
    align-items: center;
}

.speech-bubble {
    background: rgba(255, 255, 255, 0.8);
    display: inline-block;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 0 8px 5px rgba(0, 0, 0, 0.1);
    text-align: center;
    position: relative;
    transform: scale(0);
    transform-origin: 100% 50%;
    animation-fill-mode: forwards;
    animation: animateSpeechBubble 35s linear infinite;
}
.speech-bubble::before {
    content: "";
    display: block;
    width: 0;
    border-style: solid;
    border-width: 15px;
    border-color: transparent transparent rgba(255, 255, 255, 0.8) rgba(255, 255, 255, 0.8);
    /* following 3 lines of code positions pointy part of speech bubble */
    position: absolute;
    top: 30%;
    left: 100%;
}
@keyframes animateSpeechBubble {
    10% {transform: scale(1);}
    80% {transform: scale(1);}
    90% {transform: scale(0);}
    100% {transform: scale(0);}
}
.about-speech-bubble {
    width: 15%;
    z-index: 0;
}
.about-speech-bubble:hover {
    cursor: pointer;
    transform: scale(1);
    font-weight: 500;
    box-shadow: 0 8px 5px rgba(0, 0, 0, 0.3);
}

