.contact {
    background: rgba(11, 17, 139, 0.75);
    height: 100vh;
    position: relative;
    overflow: hidden;
}

.contact-heading {
    margin-top: 115px;
    position: relative;
    z-index: 2;
}

.bubble {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1), inset 0px 5px 15px 2px rgba(255, 255, 255, 0.7);
    position: absolute;
    z-index: 1;
}
.bubble1 {
    left: 10%;
    animation: animateBubble1Initial 15s linear 1, animateBubble 30s linear infinite 15s, moveSideways 3s ease-in-out infinite alternate;
}
.bubble2 {
    left: 30%;
    transform: scale(0.7);
    opacity: 70%;
    animation: animateBubble 28s linear infinite, moveSideways 2s ease-in-out infinite alternate;
}
.bubble3 {
    left: 50%;
    top: 100%;
    transform: scale(0.6);
    opacity: 60%;
    animation: animateBubble 28s linear infinite 5s, moveSideways 2s ease-in-out infinite alternate;
}
.bubble4 {
    right: 30%;
    transform: scale(0.7);
    opacity: 70%;
    animation: animateBubble4Initial 10s linear 1, animateBubble 28s linear infinite 10s, moveSideways 3s ease-in-out infinite alternate;
}
.bubble5 {
    right: 8%;
    animation: animateBubble 30s linear infinite, moveSideways 3s ease-in-out infinite alternate;
}
@keyframes animateBubble1Initial {
    0% {top: 80%;}
    100% {top: -100%;}
}
@keyframes animateBubble4Initial {
    0% {top: 50%;}
    100% {top: -100%;}
}
@keyframes animateBubble {
    0% {margin-top: 100%;}
    100% {margin-top: -100%;}
}
@keyframes moveSideways { 
    0% {margin-left: 0px;}
    100% {margin-left: 60px;}
}

.starfish-speech {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    margin-left: 65%;
    position: fixed;
    z-index: 0;
}
.starfishy {
