.home {
  height: 100%;
  padding-top: 115px;
  background-color: transparent;
  overflow: hidden; /* hides horizonal scroller when cloud moves off screen to the right */
  position: relative;
  text-align: center;
}

/* base of cloud */
.cloud {
  width: 300px;
  height: 100px;
  border-radius: 50px;
  background-color: #fff;
  box-shadow: 0 8px 5px rgba(0, 0, 0, 0.05);
}
/* left clump of cloud */
.cloud:before {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  top: -50px;
  left: 45px;
  background-color: #fff;
  content: "";
}
/* right clump of cloud */
.cloud:after {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  position: absolute;
  top: -75px;
  right: 40px;
  background-color: #fff;
  content: "";
}

/* cloud animations */
.cloud1 {
  opacity: 0.2;
  transform: scale(0.5);
  position: relative;
  z-index: 0; /* by default when transform is used */
  -webkit-animation: animateCloud1Initial 30s 1,
    animateCloud1Loop 60s linear infinite 30s;
  -moz-animation: animateCloud1Initial 30s 1,
    animateCloud1Loop 60s linear infinite 30s;
  -o-animation: animateCloud1Initial 30s 1,
    animateCloud1Loop 60s linear infinite 30s;
  animation: animateCloud1Initial 30s 1,
    animateCloud1Loop 60s linear infinite 30s;
}
@-webkit-keyframes animateCloud1Initial {
  0% {
    left: 60%;
  }
  100% {
    left: 100%;
  }
}
@-moz-keyframes animateCloud1Initial {
  0% {
    left: 60%;
  }
  100% {
    left: 100%;
  }
}
@-o-keyframes animateCloud1Initial {
  0% {
    left: 60%;
  }
  100% {
    left: 100%;
  }
}
@keyframes animateCloud1Initial {
  0% {
    left: 60%;
  }
  100% {
    left: 100%;
  }
}
@-webkit-keyframes animateCloud1Loop {
  0% {
    left: -30%;
  }
  100% {
    left: 100%;
  }
}
@-moz-keyframes animateCloud1Loop {
  0% {
    left: -30%;
  }
  100% {
    left: 100%;
  }
}
@-o-keyframes animateCloud1Loop {
  0% {
    left: -30%;
  }
  100% {
    left: 100%;
  }
}
@keyframes animateCloud1Loop {
  0% {
    left: -30%;
  }
  100% {
    left: 100%;
  }
}

