}
.dive {
  color: rgba(255, 255, 255, 0.755);
  cursor: pointer;
}
.wave {
  height: 250px;
  background: rgba(49, 49, 218, 0.65);
  margin-top: -10%;
  -webkit-animation: animateWave 5s ease-in-out infinite;
  -moz-animation: animateWave 5s ease-in-out infinite;
  -o-animation: animateWave 5s ease-in-out infinite;
  animation: animateWave 5s ease-in-out infinite;
}
@-webkit-keyframes animateWave {
  0%,
  100% {
    clip-path: polygon(
      0 76%,
      13% 74%,
      29% 75%,
      44% 80%,
      57% 86%,
      71% 87%,
      85% 87%,
      100% 82%,
      100% 100%,
      0% 100%
    );
  }
  50% {
    clip-path: polygon(
      0 78%,
      12% 85%,
      23% 90%,
      32% 91%,
      49% 86%,
      67% 79%,
      83% 77%,
      100% 79%,
      100% 100%,
      0% 100%
    );
  }
}
